const List = require('./list');

const META_DATA = {
  height: {
    ios: 5.2,
    android: 4.38,
  },
};
const platform = $.os.ios ? 'ios' : 'android';

class Datapicker {
  constructor(o) {
    this.option = o;

    const today = new Date();
    this.year = this.option.year || today.getFullYear();
    this.month = this.option.month || today.getMonth() + 1;
    this.day = this.option.day || today.getDate();

    const str = `<div class="data-datapicker-modal dp-hide"><div class="data-datapicker-box">
            <div class="data-datapicker-head">
                <div class="data-datapicker-confirm">确认</div>
                <div class="data-datapicker-cancel">取消</div>
            </div>
            <div class="data-datapicker-body">
            </div>
        </div></div>`;

    this.$wrap = $(str);
    this.$body = this.$wrap.find('.data-datapicker-body');
    
    const $year = new List({type: 'year', number: this.year});
    const $month = new List({type: 'month', number: this.month})
    const $day = new List({type: 'day', number: this.day})
    this.$body.append($year.$wrap, $month.$wrap, $day.$wrap);
    $('body').append(this.$wrap);
    this.bindEvent();
  }
  show() {
    this.$wrap.removeClass('dp-hide');
    return this;
  }
  hide() {
    this.$wrap.addClass('dp-hide');
    return this;
  }
  bindEvent() {
  }
}

module.exports = Datapicker;



