class List {
    constructor(o) {
        this.type = o.type;
        this.number = o.number;

        const str = `<div class="data-datapicker-${this.type}">${this.number}</div>`;
        this.$wrap = $(str);

        this.bindEvent();
        return this;
    }
    bindEvent() {
        this.$wrap.on('touchend', () => {
            this.number++;
            this.$wrap.html(this.number);
        });
    }
}

module.exports = List;



